/* SIBLING FADE: fade out siblings around a hovered item */

.sibling-fade { visibility: hidden; }
/* Prevents :hover from triggering in the gaps between items */

.sibling-fade > * { visibility: visible; }
/* Brings the child items back in, even though the parent is `hidden` */

.sibling-fade > * { -webkit-transition: opacity 150ms linear 100ms, -webkit-transform 150ms ease-in-out 100ms; transition: opacity 150ms linear 100ms, -webkit-transform 150ms ease-in-out 100ms; -o-transition: opacity 150ms linear 100ms, transform 150ms ease-in-out 100ms; transition: opacity 150ms linear 100ms, transform 150ms ease-in-out 100ms; transition: opacity 150ms linear 100ms, transform 150ms ease-in-out 100ms, -webkit-transform 150ms ease-in-out 100ms; }
/* Makes the fades smooth with a slight delay to prevent jumps as the mouse moves between items */

.sibling-fade:hover > * { opacity: 0.4; -webkit-transform: scale(0.9); -ms-transform: scale(0.9); transform: scale(0.9); }
/* Fade out all items when the parent is hovered */

.sibling-fade > *:hover { opacity: 1; -webkit-transform: scale(1); -ms-transform: scale(1); transform: scale(1); -webkit-transition-delay: 0ms, 0ms; -o-transition-delay: 0ms, 0ms; transition-delay: 0ms, 0ms; }
/* Fade in the currently hovered item */


